<template>
  <div id="app">
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>
<script>
import StyleEditor from './components/StyleEditor'
import ResumeEditor from './components/ResumeEditor'
import './assets/reset.css'

let isPc = (function () {
  var userAgentInfo = navigator.userAgent;
  var Agents = ["Android", "iPhone",
    "SymbianOS", "Windows Phone",
    "iPad", "iPod"
  ];
  var flag = true;
  for (var v = 0; v < Agents.length; v++) {
    if (userAgentInfo.indexOf(Agents[v]) > 0) {
      flag = false;
      break;
    }
  }
  return flag;
}());
let getDateDiff = function (startDate, endDate) {
  var startTime = new Date(Date.parse(startDate.replace(/-/g, "/"))).getTime();
  var endTime = new Date(Date.parse(endDate.replace(/-/g, "/"))).getTime();
  var dates = Math.abs((startTime - endTime)) / (1000 * 60 * 60 * 24);
  return dates;
}
document.title += getDateDiff((new Date()).getFullYear() + '-' + ((new Date()).getMonth() + 1) + '-' + (new Date()).getDate(), '2020-01-04') + 1 + '天';
export default {
  name: 'app',
  components: {
    StyleEditor,
    ResumeEditor
  },
  data() {
    return {
      interval: 1,
      currentStyle: '',
      enableHtml: false,
      fullStyle: [
        `/*
* 好久不见。
* 我猜你是用${isPc ? '电脑' : '手机'}打开的。
* 什么，你问我是谁？
* 我是一段代码，李卓远写给你作为小惊喜的。
* 我才不丑，让我加个过滤效果....
*/
* {
  -webkit-transition: all .3s;
  transition: all .3s;
}
/*
* 你说界面有点黑？
* 没事，让我改个RGB。
*/
html {
  background: rgb(0,43,54) !important ;
  color: rgb(222,222,222) !important ;
}
/*字体有点密集？*/
.styleEditor {
  padding: .5em;
  border: 1px solid;
  margin: .5em;
  overflow: auto;
  ${isPc ? 'width: 48%;height: 96%;' : 'width: 96%;height: 50%;'}
  font-size: 14px;
  line-height:1.5;
}
/* 现在好点了吧 */
/* 这些代码颜色都一样。加点儿高亮区别来 */
.token.selector{ color: rgb(133,153,0) }
.token.property{ color: rgb(187,137,0) }
.token.punctuation{ color: yellow }
.token.function{ color: rgb(42,161,152) }
.token.comment{ color: rgb(177,177,177) }
/* 等我转个身 */
html{
  -webkit-perspective: 1000px;
          perspective: 1000px;
}
.styleEditor {
  position: fixed; 
  ${isPc ? 'left: 0;' : 'left:0;right:0;margin:auto;'}
  top: 0; 
  -webkit-transition: none; 
  transition: none;   
  ${isPc ? '-webkit-transform: rotateY(10deg) translateZ(-100px) ;transform: rotateY(10deg) translateZ(-100px) ;' : '-webkit-transform: rotateX(-10deg) translateZ(-100px) ;transform: rotateX(-10deg) translateZ(-100px) ;'}
  ${isPc ? '' : '-webkit-transform-origin: 50% 0% 0;transform-origin: 50% 0% 0;'}
}

/* 再来一张信纸，写下一些李卓远想说的话 */
.resumeEditor{
  position: fixed; 
  ${isPc ? 'right: 0;' : 'left:0;right:0;margin:auto;'}
  ${isPc ? 'top: 0;' : 'bottom:2%;'}
  padding: .5em;  
  ${isPc ? 'margin: .5em;' : ''}
  ${isPc ? 'width: 48%;height: 96%;' : 'width: 96%;height: 50%;'}
  border: 1px solid;
  color: #222;
  overflow: auto;
  font-size: 14px;
  line-height:1.5;
  ${isPc ? '-webkit-transform: rotateY(-10deg) translateZ(-100px) ;transform: rotateY(-10deg) translateZ(-100px) ;' : '-webkit-transform: rotateX(10deg) translateZ(-100px) ;transform: rotateX(10deg) translateZ(-100px) ;'}
    ${isPc ? '' : '-webkit-transform-origin: 50% 0% 0;transform-origin: 50% 0% 0;'}
  }
/* 下面这些都是他的真心话 */


`,
        `
/* 什么李卓远写的不美观
 * 等我把 Markdown 修改成网页
 */
`,
        `
/* 改改样子 */
.resumeEditor{
  padding: 2em;
  line-height:1.8;
}
/* 调下字号和间距 */
.resumeEditor h2{
  display: inline-block;
  border-bottom: 1px solid;
  margin: 1em 0 .5em;
  font-size:18px;
}
.resumeEditor ul,.resumeEditor ol{
  list-style: none;
}
.resumeEditor ul> li::before{
  content: '•';
  margin-right: .5em;
}
.resumeEditor ol {
  counter-reset: section;
}
.resumeEditor ol li::before {
  counter-increment: section;            
  content: counters(section, ".") " ";  
  margin-right: .5em;
}
.resumeEditor blockquote {
  margin: 1em;
  padding: .5em;
  background: rgba(221,221,221,.5);
}

/* OK。快看看这个傻子说啥了吧 */

`
      ],
      currentMarkdown: '',
      fullMarkdown: `Lance × Tyche
----

还记得那是一个夜晚，我们一起去看了璀璨的灯光。

![那一夜](./img/night.jpg)

2020年01月04日。

到现在为止

已有 \`${getDateDiff((new Date()).getFullYear() + '-' + ((new Date()).getMonth() + 1) + '-' + (new Date()).getDate(), '2020-01-04') + 1}\` 天

我们在许多地方都拍了好看的照片

![1](./img/1.jpg)

![1](./img/2.jpg)

![1](./img/3.jpg)

![1](./img/4.jpg)

![1](./img/5.jpg)

每一张的你都是美美哒。

我们一起去过好多家餐厅（以下排名不分先后）
----

* 比格比萨
* 东来顺
* 局气
* 局气
* 局气
* 刀小蛮
* 肯德基
* 聚点串吧
* 柳叶刀烧烤
* 一味一诚烤鱼
* 吉野家
* 吉野家
* 吉野家
* ……

我们留下了好多好多的回忆（一起吃吃吃）

---

当然不止是吃，我们还一起打游戏

![1](./img/lushi.jpg)

一起打炉石，一起玩桌游，一起斗地主，一起头脑风暴。

![1](./img/youxi.jpg)

我们一起做了好多课设，学习了好多深度学习。

![1](./img/shenduxuexi.jpg)

在民宿我做了好吃的给你

![1](./img/minsu.jpg)

回忆来的时候，总有太多，想到了跟你在一起的每一天，
想到了跟你相拥的每一秒，我不想沉浸在过去，我想跟你一起创造更多的回忆。



> 谢谢你在我身边，陪我走过困苦，祝你生日快乐。

> \`From\` 希望跟你一起活到99的李卓远

`
    }
  },
  created() {
    this.makeResume()
  },

  methods: {
    makeResume: async function () {
      await this.progressivelyShowStyle(0)
      await this.progressivelyShowResume()
      await this.progressivelyShowStyle(1)
      await this.showHtml()
      await this.progressivelyShowStyle(2)
    },
    showHtml: function () {
      return new Promise((resolve) => {
        this.enableHtml = true
        resolve()
      })
    },
    progressivelyShowStyle(n) {
      return new Promise((resolve) => {
        let interval = this.interval
        let showStyle = (async function () {
          let style = this.fullStyle[n]
          if (!style) {
            return
          }
          // 计算前 n 个 style 的字符总数
          let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
          let prefixLength = length - style.length
          if (this.currentStyle.length < length) {
            let l = this.currentStyle.length - prefixLength
            let char = style.substring(l, l + 1) || ' '
            this.currentStyle += char
            if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
              this.$nextTick(() => {
                this.$refs.styleEditor.goBottom()
              })
            }
            setTimeout(showStyle, interval)
          } else {
            resolve()
          }
        }).bind(this)
        showStyle()
      })
    },
    progressivelyShowResume() {
      return new Promise((resolve) => {
        let length = this.fullMarkdown.length
        let interval = this.interval
        let showResume = () => {
          if (this.currentMarkdown.length < length) {
            this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1)
            // let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1]
            let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2]
            if (prevChar === '\n' && this.$refs.resumeEditor) {
              this.$nextTick(() => this.$refs.resumeEditor.goBottom())
            }
            setTimeout(showResume, interval)
          } else {
            resolve()
          }
        }
        showResume()
      })
    }
  }
}
</script>
<style scoped>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  min-height: 100%;
}

.styleEditor {
  -webkit-backface-visibility: hidden;
}
</style>
